import Look from '../../../components/look.vue'
export default {
	components: {
		Look
	},
	data() {
		return {
			title: '建党条件',
			list: [
			]
		}
	},
	created() {
		this.getData()
	},
	methods: {
		getData() {
			console.log(this.$api, '.then--------');
			var data = {
				category: "帮扶救助信息发布", 
				channel: "建档条件"
			}
			this.$axios(this.$api.getList , data , 'post')
			.then(res =>{
				console.log(res, '.then--------');
				if(res.error_code == 0){
					this.list = res.data.data
				}
			})
			.catch(res =>{
				console.log(res,'.catch--------')
			})
		},
	}
}



